.header{
    width: 100%;
    height: 50px;
    background: #3d3d75;
    position: fixed;
    z-index: 1;
    color: white;
}

@media only screen and (max-width: 600px) {
    #header_container_left{
        gap:20px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }
}

@media only screen and (min-width: 600px) {
    #header_container_left{
        gap:40px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    
}

#header_container{
    height: 100%;
    min-width: 650px;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
}

#logo{
    font-size: 35px;
}

#logo:hover{
    cursor: pointer;
}

#menu{
    height: 100%;
    display: flex;
    align-items: center;
}

.menu_item{
    height: 50%;
    display: flex;
    align-items: center;
    gap: 1px;
    user-select: none; 
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 2px;
    padding-bottom: 2px;
}

.menu_item:nth-child(1){
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
}

.menu_item:last-child{
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
}

.menu_item:hover{
    cursor: pointer; 
    background: #28284d; 
}

.menu_item_selected{
    background: #28284d; 
}

.menu_image{
    font-weight: 500;
    font-weight: 500;
    font-size: 16px;
}

.menu_text{
    font-size: 16px;
    font-weight: 500;
}

@media only screen and (max-width: 600px) {
    #need_help{
        display: none;
    }
}

.need_help{
    background: #3d3d75;
    border: none;
    color: white;
}

.need_help:hover{
    cursor: pointer;
    background: #28284d; 
}