#update_data{
    position:fixed;
    width: 100%;
    height: 70px;
    top: 50px;
    background: #f0f0f0;
    border-bottom: 1px solid #ccc;
    display:flex;
    align-items: center;
    z-index: 0;
}

#update_data_container{
    width: 90%;
    display:flex;
    margin: 0 auto;
    align-items: center;
    justify-content: space-between;
}

#update_data_container_left{
    display: flex;
    align-items: center;
    gap: 15px;
    min-width: 300px;
}

#update_data_container_left h1{
    font-size: 20px;
}

#user{
    display: flex;
    align-items: center;
    background: #eee;
    height: 25px;
    padding: 0px 8px;
    gap: 5px;
    border: 1px solid #ccc;
    border-radius: 3px;
}

#user:hover{
    cursor: pointer;
    border: 1px solid #aaa
}

.btn_left_colour{
    position: absolute;
    left:0;
    width: 10px;
    height: 100%;
    background:#3d3d75;
    border-top-left-radius: 3px;
    border-bottom-left-radius: 3px;
    
}

#add_data:hover{
    cursor: pointer;
    background:#e1e1f7;
}
